* {
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  background-color: #fffdee;
  background-size: cover;
  background-attachment: fixed;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  overflow: hidden;
}

button {
  background-color: #0000;
  border: none;
}

.header {
  flex-direction: row;
  margin-top: 2vh;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.back_btn {
  margin-left: 10px;
}

h4 {
  margin-top: 8px;
  margin-left: 10px;
}

.camera_btn {
  width: 30vw;
  height: 40px;
  background-color: #01d281;
  border: 3.5px solid #6f573c;
  border-radius: 15px;
  position: absolute;
  bottom: 20px;
}

.ku_img {
  height: 50vh;
  margin-bottom: 15px;
}

ul {
  justify-content: space-around;
  display: flex;
}

li {
  padding: none;
  margin: none;
  list-style: none;
}

.tab {
  width: 100px;
  box-sizing: border-box;
  height: 4vh;
  background-color: bisque;
  flex: 1;
}

.custom {
  width: 500px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.restart_btn {
  position: absolute;
  bottom: 220px;
  right: 0;
}

.option_btn {
  width: 105px;
  height: 90px;
  box-sizing: border-box;
  flex: 1;
  position: relative;
}

.makeup, .background_ku {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
}

.background_ku {
  z-index: 0;
}

.makeup {
  z-index: 2;
}

.option {
  display: none;
}

.option.active {
  display: flex;
}

@media (max-width: 500px) {
  .btn {
    width: 20vw;
  }

  .custom {
    width: 100vw;
  }
}

